export function setStoredOptions(options) {
    return new Promise((resolve) => {
        chrome.storage.local.set(options, () => {
            resolve();
        });
    });
}
export function getStoredOptions() {
    const keys = ['overlaySetting', 'isActive'];
    return new Promise((resolve) => {
        chrome.storage.local.get(keys, (res) => {
            resolve(res);
        });
    });
}
